  --llm-success-color: #4caf50;
  --llm-error-color: #f44336;
  --llm-warning-color: #ff9800;
}

/* Dark theme adjustments */
body[data-jp-theme-light='false'] {
  --llm-user-bg: #1e3a5f;
}

